<template>
    <div class="goldchange" v-title='""'>
        <div class="top">
            <a href="#"><img src="./img/link.gif" alt=""></a>
        </div>
        <div class="msg">
            <p><span>{{gold}}</span></p>
            <p><span>120</span></p>
            <ul>
                <li>
                    <img src="./img/egg.gif" class="fl" alt="">
                    <div class="info">
                        {{120*num}}
                    </div>
                    <el-input-number v-model="num" :step="1" :max ='gold|maxfiler(gold)' :min='0'></el-input-number>
                </li>
            </ul>
            <el-button type="primary"  :disabled='!ischange' @click="dialogSuccess = true"></el-button>
            <!--success -->
            <el-dialog title="" :visible.sync="dialogSuccess">
                <div class="text-center changesuccess">
                    <img src="./img/success.gif" alt="">
                </div>
                <p>

                <router-link to="/eggbasket">
                         
                </router-link>
                

                </p>
              <span slot="footer" class="dialog-footer"></span>
            </el-dialog>
            <!-- rules -->
            <el-button type="text" @click="dialogVisible = true"></el-button>
            <el-dialog title="" :visible.sync="dialogVisible">
              <ul>
                  <li>120</li>
                  <li>1</li>
                  <li>1</li>
              </ul>
              <span slot="footer" class="dialog-footer"></span>
            </el-dialog>
        </div>
    </div>
</template>
<script>
    import {info} from '../../service/getdata.js'
    import {mapState} from 'vuex'
export default {
  name: 'goldchange',
  data () {
    return {
        name:'',
        address:'  ',
        gold:1000,
        num:0,
        ischange:false,
        dialogVisible: false,
        dialogSuccess:false
    }
  },
        computed: {
            ...mapState([
                'latitude'
            ]),
        },
    methods: {
        init() {
            let infojson = {
                'user_id':'1',
            }
            let x = info(infojson);
            (async function(){
                let info = await x;
                console.log(info)
            })()
        },
        alertss(){
            alert(this.latitude)
        }
    },
    watch:{
        num:function(val,oldval){
            val>0 ?this.ischange = true : this.ischange = false
        }
    },
    filters:{
        maxfiler(v){
            return Math.ceil(v/120)
        }
    },
    created() {
        // this.init()
    },
    mounted(){
        this.init();
    }
}
</script>
<style scoped>
    .top{
        height: 150px;
        background-color: #fff;
    }
    .top img{
        width: 100%;
    }
    .changesuccess img{
        width: 100%;
    }
</style>



